.inputAndButtonContainer {
    display: flex;
    gap: 12px;
}

.error {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #CC0000;
}

.input {
    max-width: 372px;
    width: 100%;
    height: 36px;
    border: 1px solid #D1D1D1;
    border-radius: 5px;
    display: block;
}

.input:focus {
    outline: none;
}

.errorInput {
    border: 1px solid #CC0000;
    border-radius: 5px;
    max-width: 372px;
    width: 100%;
    border-radius: 5px;
    display: block;
}

.button {
    width: 106px;
    height: 30px;
    left: 18px;
    top: 20px;
    background: #0066CC;
    border-radius: 3px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #FFFFFF;
    text-transform: uppercase;
}

